import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-forgett-password',
  templateUrl: './forgett-password.page.html',
  styleUrls: ['./forgett-password.page.scss'],
})
export class ForgettPasswordPage implements OnInit {
  phoneNumber = new FormControl('');

  constructor(private router: Router, public toastCtrl: ToastController) {}

  ngOnInit() {}

  back() {
    this.router.navigateByUrl('/login');
  }

  async openToast(message) {
    const toast = await this.toastCtrl.create({
      message: `${message}`,
      duration: 2500,
      position: 'middle',
    });
    toast.present();
  }

  getOtp() {
    const pattern = /^([\+0-9]{3}[0-9]{10}|[0-9]{10})$/;
    if (pattern.test(this.phoneNumber.value)) {
      localStorage.setItem('customerPhonenum', this.phoneNumber.value);
      this.router.navigateByUrl('/otp', { state: { resetPass: true } });
    } else {
      this.openToast('Enter valid phone number');
    }

  }
}
